<template>
  <label class="form-switch" @change="toggle">
    <input type="checkbox" :checked="type == 1 ? isVolume : isVibro">
    <i></i>
  </label>
</template>

<style scoped>
.form-switch {
  display: inline-block;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
}
.form-switch i {
  position: relative;
  display: inline-block;
  width: 46px;
  height: 26px;
  background-color: #e6e6e6;
  border-radius: 23px;
  vertical-align: text-bottom;
  transition: all 0.3s linear;
}
.form-switch i::before {
  content: "";
  position: absolute;
  left: 0;
  width: 42px;
  height: 22px;
  background-color: rgb(186, 186, 186);
  border-radius: 11px;
  transform: translate3d(2px, 2px, 0) scale3d(1, 1, 1);
  transition: all 0.25s linear;
}
.form-switch i::after {
  content: "";
  position: absolute;
  left: 0;
  width: 22px;
  height: 22px;
  background-color: #fff;
  border-radius: 11px;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.24);
  transform: translate3d(2px, 2px, 0);
  transition: all 0.2s ease-in-out;
}
.form-switch:active i::after {
  width: 28px;
  transform: translate3d(2px, 2px, 0);
}
.form-switch:active input:checked + i::after { transform: translate3d(16px, 2px, 0); }
.form-switch input { display: none; }
.form-switch input:checked + i { background-color: #00E6FF; }
.form-switch input:checked + i::before { transform: translate3d(18px, 2px, 0) scale3d(0, 0, 0); }
.form-switch input:checked + i::after { transform: translate3d(22px, 2px, 0); }
</style>

<script>
export default {
  props: {
    type: {
      type: Number,
      default: 0
    }
  },
  computed:{
    isVolume(){
      return this.$user.data.sound
    },
    isVibro(){
      return this.$user.data.vibrate
    }
  },
  methods: {
    toggle(){
      if (this.type == 1) {
        this.switchvolume()
      } else {
        this.switchvibro()
      }
    },

    async switchvolume(){
      this.$user.data.sound = !this.$user.data.sound
      console.log(this.$user.data.sound)
      try {
        const response = await this.$axios.post('/turnsound/', {user_id: this.$user.data.user_id,}, {withCredentials: true});
        console.log(response)
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    },
    async switchvibro(){
      this.$user.data.vibrate = !this.$user.data.vibrate
      try {
        const response = await this.$axios.post('/turnvibrate/', {user_id: this.$user.data.user_id,}, {withCredentials: true});
        console.log(response)
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    },
  }
};
</script>
